.contentAcc {
    display: flex;
}

.btnadd {
    background-color: #46AA7F;
    cursor: pointer;
    color: #ffffff;
    font-weight: 500;
    width: 8%;
    margin: 1%;
    font-size: 22px;
}

.btnadd:hover {
    background-color: #ffffff;
    color: #46AA7F;

}

.widgetLeft {
    border-right: solid 1px #888686b6;
    width: 15%;
    text-align: center;
    padding-top: 2%;
    min-height: 815px;

    .imgprofil {
        width: 100px;
        border: solid 4px #e0e0e0;
        border-radius: 10px;
        border-radius: 50%;
        max-height: 100px;
        background-color: #f9f9f9;
    }

    .navWidget {
        a {
            text-decoration: none;
            color: rgb(49, 49, 49);
            font-weight: 500;
        }

        a:hover {
            color: #46AA7F;
        }
    }



}

.layoutRight {
    width: 83%;

    .formAcc {
        display: contents;

        label {
            margin-left: 2%;
        }

        input[type="submit"] {
            margin-left: 2%;
        }

    }
}

.table {
    width: 100%;
    margin: 1%;

    th,
    td {
        padding: 8px;
        text-align: left;
    }

    td {
        background-color: #282c2a0c;
        font-size: 12px;

    }

    th {
        background-color: #b6b6b67a;
    }
}

.btndelete {
    width: auto;
    border: none;
    cursor: pointer;
}


.show-all-button {
    background-color: #282c2a0c;
    text-align: center;
    font-size: 20px;
    cursor: pointer;
}

.headtab {
    border-bottom: solid 1px #7c6d6d70;

    h2 {
        margin-left: 5%;
    }

}

.legend {
    display: flex;
    margin-top: 5px;
    margin-left: 5px;
}

.legend-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    margin-right: 1%;
}

.legend-color {
    width: 15px;
    height: 15px;
    margin-right: 2px;
}

.repos-color {
    background-color: rgb(44, 41, 41);
    /* Couleur pour repos */
}

.course-color {
    background-color: rgb(99, 105, 160);
    /* Couleur pour course */
}

.rm-color {
    background-color: rgb(68, 107, 71);
    /* Couleur pour renforcement musculaire */
}

.color-square {
    width: 10px;
    height: 10px;
    display: inline-block;
    margin-right: 5px;
}

@media screen and (max-width: 640px) {
    .widgetLeft {
        display: none;
    }

    .layoutRight {
        width: 100%;
    }

    .table tr td:nth-child(3),
    .table tr td:nth-child(4),
    .table tr td:nth-child(5) {
        display: none;
    }

    .table th:nth-child(3),
    .table th:nth-child(4),
    .table th:nth-child(5) {
        display: none;
    }

}