.formStat {
    display: flex;
    width: 10%;
    text-align: center;
    margin: auto;
    margin-bottom: 1%;

    div {
        margin: 0 5%;
    }

    input {
        margin: 1%;
        border: 1px solid #8b8b8b;
    }
}

.statbtn {
    border-bottom: solid 1px #969696;
    margin: auto;
    width: 95%;
}

.statplus {
    display: flex;
    margin-left: 2%;
}

.statsdetail {
    margin: 2% 5%;
}

.stat {
    margin-bottom: 2%;
}

.btnstats {
    cursor: pointer;
}