.log {
    display: flex;
    height: 100vh;
    /* Utilise la hauteur totale de la fenêtre */
}

.leftlog,
.rightlog {
    flex: 1;

    /* Partage de l'espace de manière égale */
    .imgleft {
        width: 100%;
    }
}

.leftlog {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
        width: 20%;
    }

}

.rightlog {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Styles spécifiques aux éléments */
.formLog {
    max-width: 400px;
    text-align: center;
}

input[type="email"],
input[type="password"],
input[type="text"],
button {
    width: 100%;
    padding: 10px;
    /* Ajustement de la hauteur du bouton */
    margin: 5px 0;
    border: solid 1px #46AA7F;
    border-radius: 5px;
    box-sizing: border-box;
    /* Inclure le padding et le border dans la largeur */
}

.buttonlog {
    background-color: #46AA7F;
    /* Couleur de fond pour le bouton */
    color: #fff;
    /* Couleur de texte pour le bouton */
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
}

.button:hover {
    background-color: #46AA7F;
    /* Couleur de fond pour le bouton au survol */
}

.logtexte {
    margin-top: 20px;
    font-size: 20px;
}

/* Style pour les liens */
a {
    color: #46AA7F;
    /* Couleur des liens */
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

.colleft {

    .aie {
        position: absolute;
        top: 8%;
        left: 22%;
        width: 7%;
    }
}

@media screen and (max-width: 640px) {
    .connect-modal {
        width: 100%;
    }

    .buttonl {
        width: 220px;
    }

    .log {
        display: inline;

        .leftlog {
            margin-top: 50%;

            img {
                width: 50%;
            }
        }

        .rightlog {
            display: none;
        }
    }
}